<template>
  <section class="page catalog" v-cloak>

    <div class="catalog__container-menu">
      <span class="catalog__span catalog__span_col">
        <v-select 
          :array="getAllCategory"
          v-model="curCategory"
        ></v-select>

        <v-select 
          :array="getAllAgeLimit"
          v-model="curAgeRest"
          :prefix="'+'"
        ></v-select>

        <div class="select">
          <p class="select__title" @click="reset">Сброс</p>
        </div>
      </span>

      <span class="catalog__span">
        <input class="catalog__input" type="text" placeholder="Enter manga name" v-model="curSearch">
        <button class="catalog__btn" @click="search">search</button>
      </span>

      <div class="catalog__current-product">
        <div class="catalog__container-img">
          <img class="catalog__img" :src="require(`@/assets/img/${ this.curProd.img }`)" :alt="curProd.img">
        </div>

        <div class="catalog__container-info">
          <h1 class="catalog__title">{{ this.curProd.title }}</h1>
          <p class="catalog__description">{{ this.curProd.description }}</p>
          <p class="catalog__category">Стоимость за шт: {{ this.curProd.price }}$</p>
          <p class="catalog__category">Категория: {{ this.curProd.category.name }}</p>
          <p class="catalog__num-page">Возростное ограничение: {{ this.curProd.ageLimit.name }}+</p>
          <span class="catalog__span catalog__span_quantity">
            <p class="catalog__plus" @click="plus">+</p>
            <p class="catalog__quantity">{{ this.curQuantity }}</p>
            <p class="catalog__minus" @click="minus">-</p>
            <p class="catalog__total">Общая цена: {{ this.totalSum }}$</p>
          </span>
          <button class="catalog__btn_opt" @click="pushCart">Купить</button>
        </div>
      </div>
    </div>

    <div class="catalog__container-products">
      <v-product 
        v-for="el in productArr" :key="el" 
        @click="selectProd( el ), curQuantity = 1"
        :el="el"
      ></v-product>
    </div>

  </section>
</template>

<script>
import vProduct from '@/scripts/components/vProduct.vue'
import vSelect from '@/scripts/components/vSelect.vue'
import { mapGetters, mapMutations } from 'vuex'

export default {
  name: 'CatalogView',

  data: () => ({
    curAgeRest: 'Возраст',
    curCategory: 'Категория',
    productArr: null,
    curSearch: null,
    curQuantity: 1,
    curProd: null,
  }),

  watch: {
    curCategory() {
      this.productArr = this.validProduct
    },

    curAgeRest() {
      this.productArr = this.validProduct
    },
  },
  
  methods: {
    ...mapMutations([
      'addProdInCart',
      'setError'
    ]),

    selectProd( element ){
      this.curProd = element
    },

    plus() {
      this.curQuantity + 1 >= this.curProd.quantityStore 
        ? this.curQuantity = this.curProd.quantityStore
        : this.curQuantity++
    },

    minus() {
      this.curQuantity - 1 <= 1 
        ? this.curQuantity = 1 
        : this.curQuantity--
    },

    reset() {
      this.productArr = this.validProduct
      this.curCategory = 'Категория'
      this.curAgeRest = 'Возраст'
      this.curSearch = ''
    },

    pushCart() {
      const obj = this.getAllProductInCart.find( el => el.id === this.curProd.id );

      if( !obj ) this.addProdInCart({ 
        quantity: this.curQuantity,
        ...this.curProd 
      })
    },

    search() {
      this.productArr = this.validProduct
      this.productArr = this.productArr.filter( el => el.title.toLowerCase().includes( this.curSearch.toLowerCase() ) )
    },
  },

  computed: {
    ...mapGetters([
      'getAllProductInCart',
      'getAllAgeLimit',
      'getAllCategory',
      'getAllProduct',
    ]),

    selectedOption () {
      return this.options[ this.selectedOptionIndex ]
    },

    totalSum() {
      return this.curProd.price * this.curQuantity
    },

    validProduct() {
      let arr = this.getAllProduct

      if( this.curCategory !== 'Категория' ) {
        arr = arr.filter( el => el.category.name === this.curCategory )
      }

      if( this.curAgeRest !== 'Возраст' ) {
        arr = arr.filter( el => el.ageLimit.name >= this.curAgeRest )
      }

      return arr
    }
  },

  components: {
    'v-product': vProduct,
    'v-select': vSelect,
  },

  created() {
    const currItem = localStorage.getItem( 'current-prod' )
    this.curProd = currItem 
      ? JSON.parse( currItem ) 
      : this.getAllProduct[0]

    this.productArr = this.validProduct
  },

  mounted() {
    
  }
}
</script>