<template>
  <div class="select">

    <p class="select__title" @click="isOpen = !isOpen">{{ modelValue }}</p>

    <p :class="['select__arrow', { 'active': isOpen }]" @click="isOpen = !isOpen">&#43;</p>

    <div class="select__list" v-if="isOpen">
      <p 
        @click="$emit( 'update:modelValue', el.name ), isOpen = !isOpen"
        :class="['select__option', { 'active': modelValue === el.name }]" 
        :style="{ '--color': el.color }"
        v-for="el in array" :key="el"
      >
        {{ el.name }}{{ prefix }}
      </p>
    </div>

  </div>
</template>

<script>
  export default {
    name: 'vSelect',

    props: {
      array: {
        required: true,
        type: Array
      },

      prefix: {
        required: false,
        type: String
      },

      modelValue: {
        type: String
      }
    },

    data: () => ({
      isOpen: false
    }),

    watch: {
      
    },

    methods: {
      
    },

  }
</script>